html {
    font-size: 100%;
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    background-color: var(--bg-light);
    font-family: var(--font-body);
    color: var(--light-text);
    overflow-x:hidden;  
    box-sizing: border-box;
}

img {
    width: 100%;
}

body a, a:link, a:visited, a:focus, a:hover, a:active, a:-webkit-any-link {
    color: inherit;
    text-decoration:none; 
}

a {
    color:inherit;
}

ul {
    list-style: none;
    padding: 0;
}

.uppercase {
    text-transform: uppercase;
}