html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--bg-light);
  font-family: var(--font-body);
  color: var(--light-text);
  overflow-x: hidden;
  box-sizing: border-box;
}

img {
  width: 100%;
}

body a, a:link, a:visited, a:focus, a:hover, a:active, a:-webkit-any-link {
  color: inherit;
  text-decoration: none;
}

a {
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
}

.uppercase {
  text-transform: uppercase;
}

:root {
  --primary: #DA3559;
  --primary-dark: #AE2A47;
  --bg-dark: #333333;
  --bg-light: #FFFFFF;
  --dark-text: #333333;
  --light-text: #FFFFFF;
}

:root {
  --font-title: "Poppins", sans-serif;
  --font-body: "IBM Plex Sans", sans-serif;
}

h1, h2, h3, p {
  margin-top: 0;
}

nav {
  text-transform: uppercase;
}

.poppins {
  font-family: var(--font-title);
}

.header__nav__item {
  font-weight: 700;
  font-family: var(--font-title);
  font-size: 1.25rem;
  line-height: 16px;
}

.hero__title {
  font-size: 4.0625rem;
  line-height: 4.6875rem;
  text-transform: uppercase;
  font-family: var(--font-title);
}

.hero__subtitle {
  font-size: 2rem;
  line-height: 2.625rem;
  margin-bottom: 1.875rem;
}

.statement__copy {
  font-size: 2rem;
  line-height: 3.25rem;
  letter-spacing: 1%;
  text-align: center;
  max-width: 68.75rem;
}

.info__headline {
  font-family: var(--font-title);
  font-size: 3.125rem;
  line-height: 3.75rem;
  text-transform: uppercase;
  margin-bottom: 0;
}

.info__subtitle {
  font-size: 1.1875rem;
  line-height: 1.875rem;
}

.card__title {
  font-family: var(--font-title);
  font-size: 1.5rem;
  line-height: 2.125rem;
}

.card__text {
  font-size: 1.1875rem;
  line-height: 1.875rem;
}

.card__link {
  font-family: var(--font-title);
  font-size: 1.25rem;
  line-height: 1.875rem;
  color: var(--primary);
  text-decoration: underline;
  text-transform: capitalize;
}

.footer__nav__list-item {
  font-size: 0.875rem;
  line-height: 1.875rem;
  text-transform: uppercase;
  font-family: var(--font-title);
}

.footer__fineprint {
  font-size: 0.6875rem;
  line-height: 1rem;
}

.container {
  display: grid;
  grid-template-columns: minmax(1em, 1fr) minmax(200px, 1440px) minmax(1em, 1fr);
}
.container .header {
  grid-column: 1/-1;
}
.container .hero {
  grid-column: 2/2;
}
.container .statement {
  grid-column: 1/-1;
}
.container .info {
  grid-column: 2/2;
}
.container .cards {
  grid-column: 2/2;
}
.container .footer {
  grid-column: 2/2;
}
@media (max-width: 1000px) {
  .container .hero {
    grid-column: 1/-1;
  }
  .container .statement {
    grid-column: 1/-1;
  }
}

.btn {
  box-sizing: border-box;
  max-width: 11.625rem;
  padding: 1.25rem 1.875rem;
  background: var(--primary);
  display: flex;
  align-items: center;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1.25rem;
  line-height: 0.625rem;
  font-family: var(--font-title);
}

.btn:hover {
  background-color: var(--primary-dark);
}

@media (max-width: 1000px) {
  .header__nav__list {
    display: none;
  }

  .header__nav__close-nav {
    display: unset;
  }
}
.header {
  background-color: var(--bg-dark);
  color: var(--light-text);
  height: 10.1875rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__nav {
  max-width: 82.0625rem;
  padding: 0 4rem;
  height: 3.85625rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__nav__list {
  display: flex;
  align-items: center;
}
.header__nav__logo {
  background-image: url("./images/rakalogo.png");
  width: 9.8125rem;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
}
.header__nav__item:not(:first-child) {
  margin-left: 3.25rem;
}
.header__nav__close-nav {
  display: none;
}

@media (max-width: 1000px) {
  .header__nav__list {
    display: none;
  }

  .header__nav__close-nav {
    display: unset;
  }
}
.hero {
  background-image: url("./images/hero-photo.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 43.75rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.hero__text-container {
  grid-column: 1/1;
  align-self: center;
  justify-self: end;
  max-width: 34.375rem;
  margin-left: 25%;
}
.hero__text-container .hero__title {
  margin-bottom: 0.375rem;
}
.hero__text-container .hero__subtitle {
  margin-bottom: 1.875rem;
}
.hero__btn {
  height: 3.625rem;
  justify-self: start;
  color: var(--light-text);
}

@media (max-width: 1000px) {
  .hero {
    background-size: cover;
    background-position: center;
  }

  .hero__text-container {
    grid-column: 1/-1;
    margin: 0 auto;
  }
}
.statement {
  height: 29.4375rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: var(--bg-dark);
}
.statement__copy {
  font-size: 2rem;
  line-height: 3.25rem;
  letter-spacing: 1%;
  text-align: center;
  max-width: 68.75rem;
}
.statement::after {
  content: "";
  width: 15.375rem;
  height: 0.25rem;
  background-color: var(--primary);
}

@media (max-width: 1000px) {
  .statement {
    height: 900px;
  }

  .statement__copy {
    font-size: 30px;
  }
}
.info {
  padding: 8.9375rem 0 0;
  margin: 0 auto;
  height: 46rem;
  color: var(--dark-text);
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 75rem;
  max-height: 28.75rem;
  column-gap: 6.25rem;
}
.info__col {
  grid-column: 1/1;
}
.info__col--2 {
  grid-column: 2/2;
}
.info .info__btn {
  color: var(--light-text);
  margin-top: 2.25rem;
  height: 3.625rem;
}

@media (max-width: 1000px) {
  .info {
    grid-template-columns: 1fr;
    height: auto;
  }

  .info__col {
    grid-column: 1;
    grid-row: 2;
  }

  .info__col--2 {
    grid-column: 1;
    grid-row: 1;
  }
}
.cards {
  color: var(--dark-text);
  padding-top: 8.3125rem;
  max-width: 75rem;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, minmax(7.5rem, 22.5rem));
  column-gap: 3.625rem;
}
.cards .card {
  max-height: 900px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
}
.cards .card:hover {
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.3);
}
.cards .card__copy {
  min-height: 20.3125rem;
  padding: 1.375rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.cards .card:hover .card__link {
  color: var(--primary-dark);
}

@media (max-width: 1000px) {
  .cards {
    margin-top: 500px;
  }
}
@media (max-width: 700px) {
  .cards {
    grid-template-columns: 1fr;
  }

  .cards__copy {
    min-height: 0;
  }
}
.footer {
  margin-top: 11.375rem;
  color: var(--dark-text);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.footer__nav {
  max-width: 36.5rem;
}
.footer__nav ul {
  display: flex;
  justify-content: center;
  gap: 2rem;
}
.footer__socials {
  margin-top: 2.5rem;
  display: flex;
  gap: 2rem;
}
.footer__social__list-item:hover {
  color: var(--primary-dark);
}
.footer__social__list-item .social-icon {
  width: 1.875rem;
  height: 1.875rem;
}
.footer__social__list-item .social-icon path:hover {
  fill: var(--primary-dark);
}
.footer__fineprint {
  margin: 2.5rem 0 5.4375rem;
}